// Mobile first

// Desktop first

// Grid
$grid-breakpoints: (
	xs: 0,
	sm: 640px,
	md: 960px,
	lg: 1280px,
	xl: 1600px
);

$container-max-widths: (
	lg: 1280px
);

$grid-gutter-width: 2rem;
@function env($variable) {
	@error "\"env()\" function was not provided by PostCSS or dollar-variable was used as passed argument";
}

@function media($query...) {
	@error "\"media()\" function was not provided by PostCSS or was called without arguments";
}
@mixin clearfix {
	&::after {
		content: "";
		display: table;
		clear: both;
	}
}
%reset {
	all: initial;

	// Properties dependent of browser
	quotes: "“" "”" "‘" "’";
	font-family: var(--content-font-family);
	text-align: left;
	color: var(--content-color);
	outline-color: var(--content-color);

	&:focus-visible {
		outline: 1px dotted currentColor;
		outline: 5px auto -webkit-focus-ring-color;
	}

	// Overwrite some properties
	display: block;
	box-sizing: border-box;
}

@mixin reset {
	@extend %reset;
}
@mixin retina {
	// stylelint-disable-next-line media-feature-name-no-vendor-prefix
	@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
		@content;
	}
}
@mixin triangle($direction, $width, $height, $color) {
	$top-width: 0;
	$top-color: rgba(0, 0, 0, 0);
	$right-width: 0;
	$right-color: rgba(0, 0, 0, 0);
	$bottom-width: 0;
	$bottom-color: rgba(0, 0, 0, 0);
	$left-width: 0;
	$left-color: rgba(0, 0, 0, 0);

	@if $direction == (to top) {
		$left-width: $width / 2;
		$right-width: $width / 2;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to top right) or $direction == (to right top) {
		$right-width: $width;
		$right-color: $color;
		$bottom-width: $height;
	} @else if $direction == (to right) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to bottom right) or $direction == (to right bottom) {
		$left-width: $width;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to bottom) {
		$top-width: $height;
		$top-color: $color;
		$left-width: $width / 2;
		$right-width: $width / 2;
	} @else if $direction == (to bottom left) or $direction == (to left bottom) {
		$top-width: $height;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to left) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$right-width: $width;
		$right-color: $color;
	} @else if $direction == (to top left) or $direction == (to left top) {
		$top-width: $height;
		$top-color: $color;
		$right-width: $width;
	} @else {
		$direction: false;

		@warn "Incorrect triangle direction: #{inspect($direction)}";
	}

	@if $direction {
		border-style: solid;
		border-width: $top-width $right-width $bottom-width $left-width;
		border-color: $top-color $right-color $bottom-color $left-color;
		width: 0;
		height: 0;
	}
}
@mixin visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}



































































.menu-item {
	@include reset;

	margin-left: 3rem;

	@media (max-width: 1279px) {
		margin-left: 2rem;
	}

	@media (max-width: 959px) {
		flex-shrink: 0;
		margin-left: 0;
	}
}

.button {
	@include reset;

	display: flex;
	align-items: flex-start;
	width: 100%;
	color: var(--page-header-color);
	transition: color var(--page-header-transition);
	cursor: pointer;

	&:hover {
		color: var(--page-header-hover-color);
	}

	&__icon {
		position: relative;
		flex-shrink: 0;
		margin-right: 0.5rem;
		padding: 0.125rem;
		width: 1.5rem;
		height: 1.5rem;

		@media (max-width: 959px) {
			margin-right: -2.875rem;
			margin-left: 0.375rem;
			padding: 0.625rem;
			width: 2.5rem;
			height: 2.5rem;
		}

		+ .button__title {
			@media (max-width: 959px) {
				padding-left: 2.875rem;
			}
		}
	}

	&__title {
		flex-grow: 1;
		overflow: hidden;
		margin: 0.02rem 0 -0.02rem;
		padding: (1.5rem - 1.125rem * 1.25) / 2 0;
		font-size: 1.125rem;
		line-height: 1.25;
		white-space: nowrap;
		text-overflow: ellipsis;

		@media (max-width: 959px) {
			padding: (2.5rem - 1.125rem * 1.25) / 2 1rem;
		}
	}
}

.submenu {
	position: absolute;
	margin: 15px 0 0 -1px;
	border: 1px solid var(--page-header-border-color);
	border-top-color: var(--page-header-background-color);
	padding: 0 0 0.5rem;
	max-width: 200px;
	background-color: var(--page-header-background-color);
	transition: border-color var(--page-header-transition),
	background-color var(--page-header-transition);

	@media (max-width: 959px) {
		position: relative;
		margin: 0;
		//box-shadow: inset 0 3px 3px -3px var(--page-header-border-color),
		//inset 0 -3px 3px -3px var(--page-header-border-color);
		//border-top-color: var(--page-header-border-color);
		//border-left: 0;
		//border-right: 0;
		border: 0;
		padding: 0;
		max-width: none;
	}

	&__item {
		@include reset;

		display: flex;
		align-items: flex-start;
		color: var(--page-header-color);
		transition: color var(--page-header-transition);
		cursor: pointer;

		&:hover {
			color: var(--page-header-hover-color);
		}

		&-title {
			flex-grow: 1;
			overflow: hidden;
			padding: (2rem - 1.125rem * 1.25) / 2 1rem (2rem - 1.125rem * 1.25) / 2 2rem;
			font-size: 1.125rem;
			line-height: 1.25;
			white-space: nowrap;
			text-overflow: ellipsis;

			@media (max-width: 959px) {
				padding: (2.5rem - 1.125rem * 1.25) / 2 1rem (2.5rem - 1.125rem * 1.25) / 2 2.875rem;
			}
		}
	}

	&--transition {
		&-enter-active,
		&-leave-active {
			transform-origin: 0 0;
			transition: opacity var(--page-header-transition),
			transform var(--page-header-transition);

			@media (max-width: 959px) {
				transition: none;
			}
		}

		&-enter,
		&-leave-to {
			opacity: 0;
			transform: scaleY(0);
		}
	}
}
