// Mobile first

// Desktop first

// Grid
$grid-breakpoints: (
	xs: 0,
	sm: 640px,
	md: 960px,
	lg: 1280px,
	xl: 1600px
);

$container-max-widths: (
	lg: 1280px
);

$grid-gutter-width: 2rem;
@function env($variable) {
	@error "\"env()\" function was not provided by PostCSS or dollar-variable was used as passed argument";
}

@function media($query...) {
	@error "\"media()\" function was not provided by PostCSS or was called without arguments";
}
@mixin clearfix {
	&::after {
		content: "";
		display: table;
		clear: both;
	}
}
%reset {
	all: initial;

	// Properties dependent of browser
	quotes: "“" "”" "‘" "’";
	font-family: var(--content-font-family);
	text-align: left;
	color: var(--content-color);
	outline-color: var(--content-color);

	&:focus-visible {
		outline: 1px dotted currentColor;
		outline: 5px auto -webkit-focus-ring-color;
	}

	// Overwrite some properties
	display: block;
	box-sizing: border-box;
}

@mixin reset {
	@extend %reset;
}
@mixin retina {
	// stylelint-disable-next-line media-feature-name-no-vendor-prefix
	@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
		@content;
	}
}
@mixin triangle($direction, $width, $height, $color) {
	$top-width: 0;
	$top-color: rgba(0, 0, 0, 0);
	$right-width: 0;
	$right-color: rgba(0, 0, 0, 0);
	$bottom-width: 0;
	$bottom-color: rgba(0, 0, 0, 0);
	$left-width: 0;
	$left-color: rgba(0, 0, 0, 0);

	@if $direction == (to top) {
		$left-width: $width / 2;
		$right-width: $width / 2;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to top right) or $direction == (to right top) {
		$right-width: $width;
		$right-color: $color;
		$bottom-width: $height;
	} @else if $direction == (to right) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to bottom right) or $direction == (to right bottom) {
		$left-width: $width;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to bottom) {
		$top-width: $height;
		$top-color: $color;
		$left-width: $width / 2;
		$right-width: $width / 2;
	} @else if $direction == (to bottom left) or $direction == (to left bottom) {
		$top-width: $height;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to left) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$right-width: $width;
		$right-color: $color;
	} @else if $direction == (to top left) or $direction == (to left top) {
		$top-width: $height;
		$top-color: $color;
		$right-width: $width;
	} @else {
		$direction: false;

		@warn "Incorrect triangle direction: #{inspect($direction)}";
	}

	@if $direction {
		border-style: solid;
		border-width: $top-width $right-width $bottom-width $left-width;
		border-color: $top-color $right-color $bottom-color $left-color;
		width: 0;
		height: 0;
	}
}
@mixin visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}







































































































































































































































































































.page-footer {
	@include reset;

	margin-top: auto;
	background: url("../img/footer-bg.svg") no-repeat 50% 50% / #{percentage(1497 / 1920)} auto #3e3e3e;
}

.top-sect {
	position: relative;
	overflow: hidden;
	padding: 12rem 0 2rem;

	@media (max-width: 959px) {
		padding: 4rem 0 2rem;
	}

	&::before {
		content: "";
		position: absolute;
		left: 50%;
		top: 0;
		width: 1px;
		height: 0;
		background-color: #393939;
		transform: translate(0, 3rem);
		transition: height 1500ms ease-out;

		@media (max-width: 959px) {
			content: normal;
		}
	}

	&.animate--started,
	&.animate--completed {
		&::before {
			height: 6rem;
		}
	}
}

.container {
	&--has-mb {
		margin-bottom: 3rem;

		@media (max-width: 959px) {
			margin-bottom: 2rem;
		}
	}
}

.row {
	margin-bottom: -2rem;
}

.col {
	margin-bottom: 2rem;
}

.contacts {
	dl {
		margin: 0;

		dt {
			margin-bottom: 1rem;
			font-weight: 300;
			font-size: 16px;
			line-height: 1.25;
			color: var(--content-color);
		}

		dd {
			margin: 0;
			font-weight: 500;
			font-size: 16px;
			line-height: 1.25;
			color: var(--page-background-color);
		}

		a {
			text-decoration: none;
			color: inherit;
			transition: color 0.15s ease-in-out;
		}
	}

	&__title {
		@include reset;

		margin-bottom: 2.5rem;
		font-weight: var(--content-headings-font-weight);
		font-size: var(--content-headings-font-size);
		line-height: var(--content-headings-line-height);
		color: var(--page-background-color);
	}
}

.networks {
	overflow: hidden;

	&__items {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: flex-end;
		margin: -1rem;

		@media (max-width: 959px) {
			justify-content: center;
		}
	}

	&__item {
		@include reset;

		flex-shrink: 0;
		margin: 1rem;
		color: #2b2b2b;
		transition: color 0.15s ease-in-out;
		cursor: pointer;

		&:hover {
			color: var(--page-background-color);
		}

		&-icon {
			display: block;
			width: 2.5rem;
			height: 2.5rem;

			@media (max-width: 639px) {
				width: 2rem;
				height: 2rem;
			}
		}
	}
}

.menu {
	margin-top: 57px;

	@media (max-width: 1279px) {
		margin-top: 0;
	}

	&__items {
		margin-bottom: -1rem;

		@media (max-width: 1279px) {
			display: flex;
			flex-wrap: wrap;
			margin: -0.5rem;
		}
	}

	&__item {
		margin-bottom: 1rem;

		@media (max-width: 1279px) {
			margin: 0.5rem;
		}

		&-link {
			@include reset;

			overflow: hidden;
			font-size: 16px;
			line-height: 1.25;
			text-overflow: ellipsis;
			color: var(--page-background-color);
			transition: color 0.15s ease-in-out;
			cursor: pointer;

			@media (max-width: 639px) {
				text-align: center;
			}

			&:hover {
				color: var(--content-color);
			}
		}
	}
}

.sect-menu {
	display: flex;
	flex-flow: column;

	&__title {
		@include reset;

		margin: 0 0 2rem;
		padding-bottom: 5px;
		min-width: 80px;
		font-weight: 300;
		font-size: 16px;
		line-height: 1.25;
		text-align: center;
		text-transform: uppercase;
		color: var(--content-color);
		background: linear-gradient(to right, var(--content-color), var(--content-color))
		no-repeat 50% 100% / 70px 1px;
	}

	&__items {
		margin-bottom: -1rem;

		@media (max-width: 959px) {
			display: flex;
			flex-wrap: wrap;
			margin: -0.5rem;
		}
	}

	&__item {
		margin-bottom: 1rem;

		@media (max-width: 959px) {
			margin: 0.5rem;
		}

		&-link {
			@include reset;

			overflow: hidden;
			font-weight: 300;
			font-size: 16px;
			line-height: 1.25;
			text-overflow: ellipsis;
			color: var(--content-color);
			transition: color 0.15s ease-in-out;
			cursor: pointer;

			@media (max-width: 639px) {
				text-align: center;
			}

			&:hover {
				color: var(--page-background-color);
			}
		}
	}
}

.bottom-sect {
	overflow: hidden;
	padding: 1rem 0;
}

.copyright {
	font-weight: 300;
	font-size: 16px;
	line-height: 1.25;
	color: var(--content-color);
}

.developer {
	display: inline-flex;
	flex-shrink: 0;
	align-items: center;
	vertical-align: top;

	&__label {
		flex-shrink: 0;
		margin-right: 1rem;
		font-weight: 300;
		font-size: 16px;
		line-height: 1.25;
		text-transform: uppercase;
		color: var(--content-color);
	}

	&__logo {
		@include reset;

		width: 65px;
		height: 65px;
		flex-shrink: 0;
		background: {
			image: 
					url("../img/icons/png/zingy.png");
		};
		@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
		background: {
			image: 
					url("../img/icons/png/zingy@2x.png");
		};
		}
		background: {
			repeat: no-repeat;
			position: 50% 50%;
			size: contain;
		};
		cursor: pointer;
	}
}
