// Mobile first

// Desktop first

// Grid
$grid-breakpoints: (
	xs: 0,
	sm: 640px,
	md: 960px,
	lg: 1280px,
	xl: 1600px
);

$container-max-widths: (
	lg: 1280px
);

$grid-gutter-width: 2rem;
@function env($variable) {
	@error "\"env()\" function was not provided by PostCSS or dollar-variable was used as passed argument";
}

@function media($query...) {
	@error "\"media()\" function was not provided by PostCSS or was called without arguments";
}
@mixin clearfix {
	&::after {
		content: "";
		display: table;
		clear: both;
	}
}
%reset {
	all: initial;

	// Properties dependent of browser
	quotes: "“" "”" "‘" "’";
	font-family: var(--content-font-family);
	text-align: left;
	color: var(--content-color);
	outline-color: var(--content-color);

	&:focus-visible {
		outline: 1px dotted currentColor;
		outline: 5px auto -webkit-focus-ring-color;
	}

	// Overwrite some properties
	display: block;
	box-sizing: border-box;
}

@mixin reset {
	@extend %reset;
}
@mixin retina {
	// stylelint-disable-next-line media-feature-name-no-vendor-prefix
	@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
		@content;
	}
}
@mixin triangle($direction, $width, $height, $color) {
	$top-width: 0;
	$top-color: rgba(0, 0, 0, 0);
	$right-width: 0;
	$right-color: rgba(0, 0, 0, 0);
	$bottom-width: 0;
	$bottom-color: rgba(0, 0, 0, 0);
	$left-width: 0;
	$left-color: rgba(0, 0, 0, 0);

	@if $direction == (to top) {
		$left-width: $width / 2;
		$right-width: $width / 2;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to top right) or $direction == (to right top) {
		$right-width: $width;
		$right-color: $color;
		$bottom-width: $height;
	} @else if $direction == (to right) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to bottom right) or $direction == (to right bottom) {
		$left-width: $width;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to bottom) {
		$top-width: $height;
		$top-color: $color;
		$left-width: $width / 2;
		$right-width: $width / 2;
	} @else if $direction == (to bottom left) or $direction == (to left bottom) {
		$top-width: $height;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to left) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$right-width: $width;
		$right-color: $color;
	} @else if $direction == (to top left) or $direction == (to left top) {
		$top-width: $height;
		$top-color: $color;
		$right-width: $width;
	} @else {
		$direction: false;

		@warn "Incorrect triangle direction: #{inspect($direction)}";
	}

	@if $direction {
		border-style: solid;
		border-width: $top-width $right-width $bottom-width $left-width;
		border-color: $top-color $right-color $bottom-color $left-color;
		width: 0;
		height: 0;
	}
}
@mixin visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}

































































































































































































.page-header {
	@include reset;

	position: fixed;
	left: 0;
	top: 0;
	z-index: 1000;
	width: 100%;
	height: 3.5rem;
	border-bottom: 1px solid var(--page-header-border-color);
	background-color: var(--page-header-background-color);
	transition: border-color var(--page-header-transition),
	background-color var(--page-header-transition);

	&--has-open-menu {
		.menu__items {
			transform: translateX(0);
		}
	}

	&--has-open-search {
		.menu {
			@media (min-width: 960px) {
				display: none;
			}
		}

		.logo {
			@media (max-width: 959px) {
				display: none;
			}
		}

		.search {
			margin-left: auto;

			@media (max-width: 959px) {
				flex-grow: 1;
			}

			&__input {
				@media (max-width: 959px) {
					flex-grow: 1;
				}
			}
		}
	}

	&[data-theme="dark"] {
		--page-header-color: var(--page-background-color);
		--page-header-background-color: #2b2b2b;
		--page-header-border-color: #{rgba(white, 0.1)};
	}

	&[data-theme="transparent"]:not(&--has-open-menu) {
		--page-header-color: white;
		--page-header-hover-color: #c7c7c7;
		--page-header-background-color: transparent;
		--page-header-border-color: #{rgba(white, 0.1)};
	}
}

.container-fluid {
	display: flex;
	align-items: center;
	height: 100%;

	@media (max-width: 959px) {
		padding-left: 0.5rem;
		padding-right: 0.5rem;
	}
}

.logo {
	@include reset;

	flex-shrink: 0;
	width: 134px;
	height: 3rem;
	color: var(--page-header-color);
	transition: color var(--page-header-transition);
	cursor: pointer;

	@media (max-width: 959px) {
		margin: 0 auto;
	}

	&__icon {
		width: 100%;
		height: 100%;
	}
}

.menu {
	margin-left: auto;
	min-width: 0;

	@media (max-width: 959px) {
		flex-shrink: 0;
		order: -1;
		margin-left: 0;
	}

	&__toggle {
		@include reset;

		display: none;
		align-items: center;
		justify-content: center;
		width: 2.5rem;
		height: 2.5rem;
		color: var(--page-header-color);
		transition: color var(--page-header-transition);
		cursor: pointer;

		@media (max-width: 959px) {
			display: flex;
		}

		&:hover {
			color: var(--page-header-hover-color);
		}

		&-icon {
			width: 1.5rem;
			height: 1.5rem;
		}
	}

	&__items {
		display: flex;

		@media (max-width: 959px) {
			position: fixed;
			left: 0;
			bottom: 0;
			flex-direction: column;
			overflow-y: auto;
			padding: 1rem 0;
			width: 300px;
			height: calc(100% - 3.5rem);
			border-right: 1px solid var(--page-header-border-color);
			background-color: var(--page-header-background-color);
			transform: translateX(-100%);
			transition: transform var(--page-header-transition);
		}
	}
}

.search {
	display: flex;
	flex-shrink: 0;
	align-items: center;
	margin-left: 3rem;

	@media (max-width: 1279px) {
		margin-left: 2rem;
	}

	@media (max-width: 959px) {
		margin-left: 0;
	}

	&__input {
		@include reset;

		margin-right: 0.25rem;
		border-bottom: 1px solid var(--page-header-color);
		width: 200px;
		height: 1.5rem;
		color: var(--page-header-color);
		transition: color var(--page-header-transition);

		@media (max-width: 959px) {
			margin-left: 0.25rem;
		}

		&:focus-visible {
			outline: 0;
		}
	}

	&__toggle {
		@include reset;

		display: flex;
		flex-shrink: 0;
		align-items: center;
		justify-content: center;
		width: 2.5rem;
		height: 2.5rem;
		color: var(--page-header-color);
		transition: color var(--page-header-transition);
		cursor: pointer;

		&:hover {
			color: var(--page-header-hover-color);
		}

		&-icon {
			width: 1.5rem;
			height: 1.5rem;
		}
	}
}
