// Mobile first

// Desktop first

// Grid
$grid-breakpoints: (
	xs: 0,
	sm: 640px,
	md: 960px,
	lg: 1280px,
	xl: 1600px
);

$container-max-widths: (
	lg: 1280px
);

$grid-gutter-width: 2rem;
@function env($variable) {
	@error "\"env()\" function was not provided by PostCSS or dollar-variable was used as passed argument";
}

@function media($query...) {
	@error "\"media()\" function was not provided by PostCSS or was called without arguments";
}
@mixin clearfix {
	&::after {
		content: "";
		display: table;
		clear: both;
	}
}
%reset {
	all: initial;

	// Properties dependent of browser
	quotes: "“" "”" "‘" "’";
	font-family: var(--content-font-family);
	text-align: left;
	color: var(--content-color);
	outline-color: var(--content-color);

	&:focus-visible {
		outline: 1px dotted currentColor;
		outline: 5px auto -webkit-focus-ring-color;
	}

	// Overwrite some properties
	display: block;
	box-sizing: border-box;
}

@mixin reset {
	@extend %reset;
}
@mixin retina {
	// stylelint-disable-next-line media-feature-name-no-vendor-prefix
	@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
		@content;
	}
}
@mixin triangle($direction, $width, $height, $color) {
	$top-width: 0;
	$top-color: rgba(0, 0, 0, 0);
	$right-width: 0;
	$right-color: rgba(0, 0, 0, 0);
	$bottom-width: 0;
	$bottom-color: rgba(0, 0, 0, 0);
	$left-width: 0;
	$left-color: rgba(0, 0, 0, 0);

	@if $direction == (to top) {
		$left-width: $width / 2;
		$right-width: $width / 2;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to top right) or $direction == (to right top) {
		$right-width: $width;
		$right-color: $color;
		$bottom-width: $height;
	} @else if $direction == (to right) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to bottom right) or $direction == (to right bottom) {
		$left-width: $width;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to bottom) {
		$top-width: $height;
		$top-color: $color;
		$left-width: $width / 2;
		$right-width: $width / 2;
	} @else if $direction == (to bottom left) or $direction == (to left bottom) {
		$top-width: $height;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to left) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$right-width: $width;
		$right-color: $color;
	} @else if $direction == (to top left) or $direction == (to left top) {
		$top-width: $height;
		$top-color: $color;
		$right-width: $width;
	} @else {
		$direction: false;

		@warn "Incorrect triangle direction: #{inspect($direction)}";
	}

	@if $direction {
		border-style: solid;
		border-width: $top-width $right-width $bottom-width $left-width;
		border-color: $top-color $right-color $bottom-color $left-color;
		width: 0;
		height: 0;
	}
}
@mixin visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}
$content-font-size: 16;
$content-headings-font-size: 34;

:root {
	--content-font-family: "Roboto", sans-serif;
	--content-font-weight: 400;
	--content-font-size: 16px;
	--content-line-height: 1.5;
	--content-color: #7c7c7c;
	--content-link-color: #C04000;
	--content-space: 1em;

	// Headings
	--content-headings-font-family: "Roboto", sans-serif;
	--content-headings-font-weight: 700;
	--content-headings-font-size: 34px;
	--content-headings-line-height: 1.25;
	--content-headings-color: #0a0a0a;

	--content-h1-font-size: var(--content-headings-font-size);
	--content-h2-font-size: calc(var(--content-headings-font-size) * 28 / 34);
	--content-h3-font-size: calc(var(--content-headings-font-size) * 26 / 34);
	--content-h4-font-size: calc(var(--content-headings-font-size) * 24 / 34);
	--content-h5-font-size: calc(var(--content-headings-font-size) * 22 / 34);
	--content-h6-font-size: calc(var(--content-headings-font-size) * 20 / 34);

	@media (max-width: 959px) {
		--content-font-size: 15px;
		--content-headings-font-size: 30px;
	}
}

.content {
	@include reset;

	font-family: var(--content-font-family);
	font-weight: var(--content-font-weight);
	font-size: var(--content-font-size);
	line-height: var(--content-line-height);
	word-wrap: break-word;
	color: var(--content-color);

	p, blockquote {
		// page-break-inside: avoid;
	}

	p, blockquote, ul, ol, dl, table, pre {
		margin-top: 0;
		margin-bottom: var(--content-space);
	}

	h1,h2,h3,h4,h5,h6,.h1,.h2,.h3,.h4,.h5,.h6 {
		margin-top: calc(var(--content-space) * 1.5);
		margin-bottom: var(--content-space);
		font-weight: var(--content-headings-font-weight);
		line-height: var(--content-headings-line-height);
		color: var(--content-headings-color);
	}

	h1, .h1 {
		font-size: var(--content-h1-font-size);
	}

	h2, .h2 {
		font-size: var(--content-h2-font-size);
	}

	h3, .h3 {
		font-size: var(--content-h3-font-size);
	}

	h4, .h4 {
		font-size: var(--content-h4-font-size);
	}

	h5, .h5 {
		font-size: var(--content-h5-font-size);
	}

	h6, .h6 {
		font-size: var(--content-h6-font-size);
	}

	ul,
	ol {
		padding-left: calc(var(--content-space) * 2);
	}

	li + li {
		margin-top: calc(var(--content-space) * 0.25);
	}

	ul ul,
	ul ol,
	ol ol,
	ol ul {
		margin-top: 0;
		margin-bottom: 0;
	}

	dl {
		padding: 0;

		dt {
			margin-top: var(--content-space);
			padding: 0;
			font-weight: 700;
			font-style: italic;
			font-size: 1em;
		}

		dd {
			margin-bottom: var(--content-space);
			margin-left: 0;
			padding: 0 1em;
		}
	}

	pre {
		overflow: auto;
		padding: 1em;
		font-size: 85%;
		line-height: 1.45;
		word-wrap: normal;
	}

	code {
		margin: 0;
		padding: 0.2em 0.4em;
		font-size: 85%;
	}

	> :first-child {
		margin-top: 0;
	}

	> :last-child {
		margin-bottom: 0;
	}

	&--invert {
		color: var(--page-background-color);

		h1,h2,h3,h4,h5,h6,.h1,.h2,.h3,.h4,.h5,.h6 {
			color: var(--page-background-color);
		}
	}
}
